import Location from '@/static/mbcImg/my/Image.png';
//看過 電話 聊天 點讚..
import zan from '@/static/mbcImg/my/zan.png';
import look from '@/static/mbcImg/my/look.png';
import phone from '@/static/mbcImg/my/phone.png';
import liu from '@/static/mbcImg/my/liu.png';
import shou from '@/static/mbcImg/my/shou.png';
import fen from '@/static/mbcImg/my/fen.png';
//結束
import { mapMutations, mapGetters } from 'vuex';
import kong from '@/static/mbcImg/my/kong.png';
export default {
	data() {
		return {
			Location:Location,
			activeIndex:"0",
			lists:[],
			zan:zan,
			look:look,
			kong:kong,
			phone:phone,
			liu:liu,
			shou:shou,
			fen:fen,
		}
	},
	watch: {
		GET_MY: {
			handler(a, b) {
				console.log(a, b)
				this.lists = a.zi;
				console.log(this.lists)
			},
			deep: true
		},
	},
	computed: {
	  ...mapGetters(['GET_HOME', 'QUICKNAVCO','GET_MY'])
	},
	created(){
		this.inf();
	},
	filters: {
		formatDate: function(value) {
			let date = new Date(value);
			let y = date.getFullYear();
			let MM = date.getMonth() + 1;
			MM = MM < 10 ? ('0' + MM) : MM;
			let d = date.getDate();
			d = d < 10 ? ('0' + d) : d;
			let h = date.getHours();
			h = h < 10 ? ('0' + h) : h;
			let m = date.getMinutes();
			m = m < 10 ? ('0' + m) : m;
			let s = date.getSeconds();
			s = s < 10 ? ('0' + s) : s;
			return y + '.' + MM + '.' + d ;
		}
	},
	methods:{
		getActive(index){
			this.activeIndex=index;
			this.$emit("changes",this.activeIndex);
		},
		gotomyproducts(e){
			console.log('to说明书详情')
			uni.navigateTo({
				url: '/modules/InstructionsPrice/myproduct/product-book/product-books?id='+e,
			});
		},
		inf(){//资讯
			if (uni.getStorageSync('landRegist')) {
				let landRegistLG = JSON.parse(uni.getStorageSync('landRegist')); // 读取缓存的用户信息
				console.log(landRegistLG.user.id);
				// let params = {}; // 请求总数居时 参数为空
				uni.showLoading({ // 展示loading
					title: '加载中'
				});
				uni.request({
					url: this.api2 + '/rest-rp/instruc?userId=760&page=1&instrucCustType=2', //接口地址。
					// data: params,
					method: 'GET',
					header: {
						Authorization: "Bearer " + landRegistLG.token //将token放到请求头中
					},
					success: (response) => {
						uni.hideLoading();
						console.log(response.data);
						this.zi = response.data.content.list
						this.$store.commit('setzi', this.zi);
						// this.lists = response.data.content.instKeys
					},
					fail: (error) => {
						uni.hideLoading(); // 隐藏 loading
						uni.showToast({
							title: '网络繁忙，请稍后',
							icon: 'none',
							duration: 1000
						});
						console.log(error, '网络繁忙，请稍后');
					}
				});
			}
		},
	}
	
}
